.formResults {
    position: absolute;
    z-index: 9999;
    width: 100%;
    height: 500px;
    overflow-y: visible;
    text-align: left;
    
  }
  
  .formResults li {
    background-color: #263238;
    -webkit-transition: all 0.35s ease-out;
    -o-transition: all 0.35s ease-out;
    transition: all 0.35s ease-out;
    -webkit-box-shadow: 0px 0px 10px white;
            box-shadow: 0px 0px 10px white;
   
  }
  
  .formResults li:hover {
    background-color: rgb(36, 35, 35);
    -webkit-box-shadow: 0px 0px 10px lightblue;
            box-shadow: 0px 0px 10px lightblue; /*each element*/
  }
  
  .formResults a {
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    padding: 20px 20px;
  }
  
  .resultPoster {
    width: 100px;
    margin-right: 20px;
    -ms-flex-item-align: center;
        align-self: center;
  }



